<script>
	export default {
		data() {
			return {
				imgNo: '00',
				size: 1,
				sizeHeight: 736,
			}
		},
		mounted() {
			setInterval(() => {
				this.imgAnimationShow()
			}, 4000)
			
			this.resizeIframe()
			window.addEventListener("resize", this.resizeIframe)
		},
		methods: {
			resizeIframe() {
				const width = window.innerWidth;
				const height = window.innerHeight;
				let transform = width / 517.5
				if (transform < 1) {
					this.size = transform
				} else {
					this.size = 1
				}
				this.sizeHeight = 736 * this.size
			},
			imgAnimationShow() {
				let imgNo = -1
				let imgAnimation = setInterval(() => {
					imgNo++
					if (imgNo < 10) {
						this.imgNo = "0" + imgNo
					} else {
						this.imgNo = "" + imgNo
					}
					if (imgNo >= 23) {
						clearInterval(imgAnimation)
					}
				}, 32)
			},
			back() {
				window.opener.focus()
			}
		}
	}
</script>


