	.mainContent>div>.backg {
		position: absolute;
		width: 100%;
		max-width: 1920px;
		max-height: 800px;
		height: calc(100% - 30px);
		z-index: -1;
		overflow: hidden;
		top: 30px
	}

	.mainContent>div>.backg.successBg {
		background: url("../assets/image/verify/success.png");
		background-size: cover;
		background-position: center 20%
